header {
  position: relative;
  width: 100%;
  height: 152px;
  h1 {
    position: absolute;
    left: 40px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 60px;
    font-family: 锐字锐线怒放黑简;
    color: #FFF;
  }
  h2 {
    position: absolute;
    left: 50%;
    bottom: -20px;
    transform: translateX(-50%);
    width: 920px;
    height: 98px;
    background: url(./images/title-bg.png) no-repeat center / 100% 100%;
    span:nth-child(1) {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-60%, -50%);
      font-size: 56px;
      font-family: 锐字锐线怒放黑简;
      color: #2e48aa;
    }
    span:nth-child(2) {
      position: absolute;
      top: 50%;
      right: 185px;
      transform: translateY(-50%);
      font-size: 60px;
      color: rgb(255, 210, 0);
      text-shadow: 0 0 30px rgba(255, 210, 0, .72);
    }
  }
  .user-box {
    font-size: 26px;
    position: absolute;
    right: 56px;
    bottom: 10px;
    color: #134ded;
    span {
      display: inline-block;
      width: 200px;
      height: 80px;
      line-height: 80px;
      text-align: center;
      cursor: pointer;
      .icon {
        display: inline-block;
        margin-right: 10px;
        width: 55px;
        height: 55px;
        background: url(./images/icon.png) no-repeat;
        vertical-align: middle;
      }
    }
    .user-name {
      color: #35ecfa;
    }
    .log-out {
      .icon {
        background-position: 0 -56px;
      }
    }
  }
}



