body {
    background: url(./images/bg.jpg) no-repeat center / 100% 100%;
}

main {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
}

.middle-panel-container {
    position: absolute;
    width: 1450px;
    height: 345px;
    background: url(./images/panel-bg.png) no-repeat center / 100% 100%;
    .title {
        position: absolute;
        top: -24px;
        left: 200px;
        z-index: 1;
        padding: 14px 32px;
        font-size: 32px;
        color: #FFF;
        background: #2853ff;
    }
}

/*Webkit自定义滚动条样式start*/
::-webkit-scrollbar {  /* 滚动条整体部分 */
    width:5px;
    margin-right:2px
}
::-webkit-scrollbar:horizontal {
    height:0;
}
::-webkit-scrollbar-track {  /* 外层轨道 */
    border-radius: 0;
}
::-webkit-scrollbar-track-piece {  /*内层轨道，滚动条中间部分 */
    background-color: transparent;
    border-right: 1px solid #339dff;
    border-radius: 0;
}
::-webkit-scrollbar-thumb {  /* 滑块 */
    width:7px;
    border-radius: 0;
    background: #fff;
}
::-webkit-scrollbar-corner { /* 边角 */
    width: 0;
    background-color: transparent;
}
::-webkit-scrollbar-thumb:hover { /* 鼠标移入滑块 */
    background: #bbb;
}

.com-hint {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -15px;
    font-size: 30px;
    color: #fff;
  }
  
  /* 浮窗 */
  .com-floating {
      display: none;
      position: absolute;
      top: 0px;
      left: 0px;
      padding: 10px 20px 30px;
      min-width: 300px;
      max-width: 400px;
      font-size: 26px;
      line-height: 40px;
      color: #fff;
      min-height: 100px;
      background: url(./images/frame-bg.png);
      border-radius: 20px;
      border: 2px solid #0226dd;
      z-index: 10;
      .img-top {
        position: relative;
        top: -18px;
        width: 100%;
        height: 20px;
        background: url(./images/frame-top.png) no-repeat center;
        &::before,
        &::after {
            position: absolute;
            left: -22px;
            top: -2px;
            content: url(./images/frame-bor.png);
        }
        &::after {
            left: auto;
            right: -22px;
            transform: rotate(90deg);
        }
      }
      &::before,
      &::after {
          position: absolute;
          left: 0;
          bottom: -9px;
          content: url(./images/frame-bor.png);
          transform: rotate(270deg);
      }
      &::after {
          left: auto;
          right: -4px;
          transform: rotate(180deg);
      }
  }


