.threaten-list-container {
    top: 1420px;
    left: 60px;
    .btn-more {
        cursor: pointer;
        position: absolute;
        top: -18px;
        right: 100px;
        z-index: 1;
        width: 140px;
        height: 48px;
        line-height: 48px;
        text-align: center;
        font-size: 24px;
        color: #FFF;
        background: url(./images/btn-more.png) no-repeat center / 100% 100%;
    }
    .dialog-upload {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: -18px;
        right: 48px;
        z-index: 4;
        width: 682px;
        height: 136px;
        background: url(./images/dialog-bg.png) no-repeat center / 100% 100%;
        &.hide {
            display: none;
        }
        .btn-close {
            cursor: pointer;
            position: absolute;
            top: -12px;
            right: -12px;
            width: 36px;
            height: 36px;
            background: url(./images/btn-close.png) no-repeat center / 100% 100%;
        }
        .upload-form {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
            width: 586px;
            height: 44px;
            span.file-cover {
                display: block;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                z-index: 0;
                width: 130px;
                height: 38px;
                line-height: 38px;
                text-align: center;
                font-size: 26px;
                color: #00fff6;
                border: 2px solid #2853ff;
            }
            .file {
              display: flex;
              width: 450px;
              background: #071172;
              border: 1px solid #14a1d5;
              input[type="file"] {
                  position: relative;
                  z-index: 1;
                  cursor: pointer;
                  width: 130px;
                  height: 38px;
                  outline: none;
                  opacity: 0;
              }
              p.file-name {
                flex: 1;
                height: 38px;
                line-height: 38px;
                text-align: center;
                font-size: 26px;
                color: #FFF;
              }
            }
            button {
                cursor: pointer;
                width: 98px;
                height: 38px;
                line-height: 38px;
                outline: none;
                border: none;
                background: url(./images/btn-upload.png) no-repeat center / 100% 100%;
                font-size: 26px;
                color: #FFF;
                text-align: center;
            }
        }
    }
    .table {
        position: relative;
        z-index: 1;
        width: 1320px;
        margin: 68px auto 0 auto;
        .header {
            display: flex;
            li {
                width: 16.6666%;
                text-align: center;
                font-size: 32px;
                color: #9dc9ff;
            }
        }
        .row {
            width: 100%;
            height: 185px;
            margin-top: 28px;
            overflow: hidden;
            .col {
                display: flex;
                width: 100%;
                height: 75px;
                background: url(./images/table-bg.png) no-repeat center / 100% 100%;
                &:hover {
                    background: url(./images/table-item-emphasis.png) no-repeat center / 100% 100%;
                }
                li {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 16.6666%;
                    font-size: 26px;
                    color: #FFF;
                    &.attack,
                    &.intercept,
                    &.attachment,
                    &.upload {
                        cursor: pointer;
                    }
                    &.attack:hover,
                    &.intercept:hover {
                        text-decoration: underline;
                    }
                    &.attachment {
                        a {
                            color: #ffd200;
                            text-decoration: none;
                        }
                        a:hover {
                            text-decoration: underline;
                        }
                    }
                    &.upload {
                        span {
                            display: block;
                            width: 90px;
                            height: 40px;
                            line-height: 40px;
                            text-align: center;
                            background: url(./images/btn-upload.png) no-repeat center / 100% 100%;
                        }
                    }
                }
            }
        }
    }
}



