.warning-container {
    display: flex;
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);
    width: 3240px;
    height: 102px;
    .title {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 274px;
        height: 102px;
        background: url(./images/pane.png) no-repeat center / 100% 100%;
        span:nth-child(1) {
            width: 82px;
            height: 82px;
            background: url(./images/icon-warning.png) no-repeat center / 100% 100%;
        }
        span:nth-child(2) {
            font-size: 32px;
            color: #fc4341;
        }
    }
    .content-wrapper {
        position: relative;
        width: 2960px;
        margin-left: 12px;
        background: url(./images/panel.png) no-repeat center / 100% 100%;
        .btn-detail {
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            right: 20px;
            width: 140px;
            height: 52px;
            span:nth-child(1) {
                width: 62px;
                height: 62px;
                background: url(./images/icon-warning.png) no-repeat center / 100% 100%;
            }
            span:nth-child(2) {
                font-size: 24px;
                color: #fc4341;
            }
        }
        .list-wrapper {
            width: 92%;
            height: 100%;
            margin-left: 60px;
            overflow: hidden;
            .list {
                height: 102px;
                line-height: 102px;
                li {
                    display: inline-block;
                    margin-right: 200px;
                    font-size: 32px;
                    color: #FFF;
                }
            }
        }
    }
}

/*Plugin CSS*/

.str_wrap {
    overflow: hidden; //zoom:1;
    width: 100%;
    font-size: 12px;
    line-height: 16px;
    position: relative;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;
    white-space: nowrap;
}

.str_wrap.str_active {
}

.str_move {
    white-space: nowrap;
    position: absolute;
    top: 0;
    left: 0;
    cursor: move;
}

.str_move_clone {
    display: inline-block; //display:inline;
    //zoom:1;
    vertical-align: top;
    position: absolute;
    left: 100%;
    top: 0;
}

.str_vertical .str_move_clone {
    left: 0;
    top: 100%;
}

.str_down .str_move_clone {
    left: 0;
    bottom: 100%;
}

.str_vertical .str_move,
.str_down .str_move {
    white-space: normal;
    width: 100%;
}

.str_static .str_move,
.no_drag .str_move,
.noStop .str_move {
    cursor: inherit;
}

.str_wrap img {
    max-width: none !important;
}



