.safeindex-container {
  position: absolute;
  left: 60px;
  top: 182px;
  width: 800px;
}

.safeindex-panel {
  position: relative;
  width: 800px;
  height: 362px;
  margin-bottom: 24px;
  background: url(./images/safeindex-panel-bg.png) no-repeat center / 100% 100%;
  .title {
    display: flex;
    align-items: center;
    position: absolute;
    left: 50%;
    top: -10px;
    transform: translateX(-50%);
    span:nth-child(1) {
      font-size: 32px;
      color: #FFF;
    }
    span:nth-child(2) {
      font-size: 60px;
      color: #ffd200;
      margin-left: 80px;
    }
  }
}

.net-safeindex-panel {
  .wrapper {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .item {
      width: 200px;
      height: 220px;
      margin: 0 16px;
    }
  }
}

.terminal-safeindex-panel {
  height: 400px;
  .wrapper {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -45%);

    .item {
      display: flex;
      width: 700px;
      height: 90px;


      .chart {
        width: 90px;
        height: 90px;
        background: url(./images/liquidfill-bg.png) no-repeat center / 100% 100%;
      }

      .label-wrapper {
        display: flex;
        flex-direction: column;
        width: 546px;
        height: 90px;
        span:nth-child(1) {
          font-size: 48px;
          color: #00e6ff;
          text-shadow: 0 0 15px #00e6ff;
        }
        span:nth-child(2) {
          font-size: 30px;
          color: #a4d4ff;
          margin-top: 6px;
        }
        &.left {
          align-items: flex-start;
          background: url(./images/panel1-left-bg.png) no-repeat center / 100% 100%;
          span {
            margin-left: 16px;
          }
        }
        &.right {
          align-items: flex-end;
          background: url(./images/panel1-right-bg.png) no-repeat center / 100% 100%;
          span {
            margin-right: 16px;
          }
        }
      }
    }
  }
}

.data-safeindex-panel {
  height: 400px;
  .wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);
    display: flex;
    flex-wrap: wrap;
    width: 718px;

    .item {
      position: relative;
      width: 344px;
      height: 120px;
      margin-bottom: 24px;
      background: url(./images/panel2-bg.png) no-repeat center / 100% 100%;
      overflow: hidden;
      span {
        display: block;
        position: absolute;
        z-index: 1;
      }
      span:nth-child(1) {
        right: 24px;
        top: 12px;
        font-size: 48px;
        color: #00e6ff;
        text-shadow: 0 0 32px #00e6ff;
      }
      span:nth-child(2) {
        width: 86%;
        left: 50%;
        bottom: 16px;
        transform: translateX(-50%);
        font-size: 32px;
        text-align: right;
        color: #9dc9ff;
      }
      .liquidfill-panel {
        position: absolute;
        left: 0;
        top: -80px;
        z-index: 0;
        width: 344px;
        height: 500px;
      }
    }
    .item:nth-child(2n) {
      margin-left: 24px;
    }
  }
}



